import CampaignRoundedIcon from "@mui/icons-material/CampaignRounded";
import ForumRoundedIcon from "@mui/icons-material/ForumRounded";
import GridViewRoundedIcon from "@mui/icons-material/GridViewRounded";
import GroupWorkOutlinedIcon from "@mui/icons-material/GroupWorkOutlined";
import PeopleRoundedIcon from "@mui/icons-material/PeopleRounded";
import TourRoundedIcon from "@mui/icons-material/TourRounded";
import { Collapse, styled, Typography, useMediaQuery } from "@mui/material";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import React, { useContext, useEffect, useState } from "react";
import { useMatch, useNavigate } from "react-router-dom";
import { DraweContext } from "./Drawer";

const MyListItemText = styled(ListItemText)(({ theme, active, ischild }) => ({
  "& .MuiListItemText-primary": {
    textTransform: "uppercase",
    color: "rgb(99, 115, 129)",
    fontWeight: "300",
  },
  ...(ischild && {
    "& .MuiListItemText-primary": {
      fontFamily: "Cairo",
      textTransform: "uppercase",
      fontSize: ".8rem",
      color: theme.palette.text.buttonBg,
    },
  }),
  ...(active && {
    "& .MuiListItemText-primary": {
      color: theme.palette.primary.main,
      textTransform: "uppercase",
      fontWeight: "500",
      ...(ischild && {
        fontSize: ".8rem",
        fontFamily: "Cairo",
      }),
    },
  }),
}));

const MyListItemIcon = styled(ListItemIcon)(({ theme, active, isfather }) => ({
  color: theme.palette.text.buttonBg,
  ...(isfather && {
    color: theme.palette.text.primary,
  }),
  ...(active && {
    color: theme.palette.primary.main,
  }),
}));

const DrawerItems = () => {
  const n = useNavigate();
  const { ToggleOpen } = useContext(DraweContext);
  const matches = useMediaQuery((theme) => theme.breakpoints.down("sm"));

  const HandlMacth = (route) => {
    let match = useMatch(`${route}`);
    return match;
  };
  const navigate = (link) => {
    n(link);
    if (matches) ToggleOpen();
  };
  return (
    <List sx={{ py: 0 }}>
      {items.map((item, index) =>
        item.withChilds ? (
          <LinkChild
            item={item}
            key={index}
            HandlMacth={HandlMacth}
            navigate={navigate}
          />
        ) : (
          <NoLinkChild
            item={item}
            key={index}
            HandlMacth={HandlMacth}
            navigate={navigate}
          />
        )
      )}
    </List>
  );
};

const LinkChild = ({ item, HandlMacth, navigate }) => {
  const [open, setopen] = useState(false);
  let match = useMatch(`${item.link}/*`);
  useEffect(() => {
    if (match) setopen(true);
    else setopen(false);
  }, [match]);
  const handleToggle = () => {
    setopen(true);
    navigate(item.childs[0].link);
  };

  return (
    <>
      <ListItem
        button
        onClick={handleToggle}
        sx={open ? classes.activeBorder : {}}
      >
        <MyListItemIcon active={open ? "true" : undefined} isfather={"true"}>
          {item.icon}
        </MyListItemIcon>
        <MyListItemText primary={item.label} active={open ? "true" : undefined} />
      </ListItem>
      <Collapse in={open} sx={classes.shadow}>
        {item.childs.map((child, index) => (
          <ListItem key={index} button onClick={() => navigate(child.link)}>
            <MyListItemIcon active={HandlMacth(child.link) ? "true" : undefined}>
              <Typography sx={classes.iconChild} variant="span">
                {child.icon}
              </Typography>
            </MyListItemIcon>
            <MyListItemText
              ischild={"true"}
              primary={child.label}
              active={HandlMacth(child.link) ? "true" : undefined}
            />
          </ListItem>
        ))}
      </Collapse>
    </>
  );
};

const NoLinkChild = ({ item, HandlMacth, navigate }) => {
  return (
    <ListItem
      button
      onClick={() => {
        navigate(item.link);
      }}
      sx={HandlMacth(item.link) ? classes.activeBorder : {}}
    >
      <MyListItemIcon active={HandlMacth(item.link)  ? "true" : undefined} isfather={"true"}>
        {item.icon}
      </MyListItemIcon>
      <MyListItemText primary={item.label} active={HandlMacth(item.link)} />
    </ListItem>
  );
};

export default DrawerItems;

const classes = {
  activeBorder: {
    backgroundColor: "primary.lightBg",
    borderLeft: "2px solid",
    borderColor: "primary.main",
  },
  shadow: {
    boxShadow: "inset 0px 11px 8px -14px #ccc, inset 0px -6px 8px -10px #ccc",
  },
  iconChild: {
    marginLeft: "4px",
    width: "16px",
    height: "16px",
    lineHeight: "0px",
    fontSize: ".6rem",
    border: "2px solid",
    borderRadius: "50%",
    fontWeight: "900",
    display: "grid",
    placeItems: "center",
    fontFamily: "Cairo",
  },
};

const items = [
  {
    label: "Overview",
    icon: <GridViewRoundedIcon />,
    link: "/",
    active: true,
  },
  {
    withChilds: true,
    childs: [
      {
        label: "Group A",
        link: "/groups/A",
        icon: "A",
      },
      {
        label: "Group B",
        link: "/groups/B",
        icon: "B",
      },
    ],
    label: "Groups",
    link: "groups",
    icon: <GroupWorkOutlinedIcon />,
  },
  {
    label: "Tours",
    link: "tours",
    icon: <TourRoundedIcon />,
  },
  {
    label: "Users",
    link: "users",
    icon: <PeopleRoundedIcon />,
  },
  {
    label: "Chat",
    link: "chat",
    icon: <ForumRoundedIcon />,
  },
  {
    label: "Advertisement",
    link: "advertisement",
    icon: <CampaignRoundedIcon />,
  },
];
