import AddCircleOutlineRoundedIcon from "@mui/icons-material/AddCircleOutlineRounded";
import CloseIcon from "@mui/icons-material/Close";
import {
  Box,
  Chip,
  createTheme,
  DialogActions,
  DialogContent,
  Grid,
  IconButton,
  Stack,
  ThemeProvider,
} from "@mui/material";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import FormControl from "@mui/material/FormControl";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormLabel from "@mui/material/FormLabel";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import Typography from "@mui/material/Typography";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useFormik } from "formik";
import React, { useContext, useEffect, useRef } from "react";
import { StatusContext } from "../../../App";
import { StextField } from "../../../Tools/StyledComp";
import { ToursContext } from "./Tours";

const theme = createTheme({
  palette: {
    primary: {
      main: "#FF5656",
      bg: "#FF56564f",
      lightBg: "#FF565612",
    },
    secondary: {
      main: "#637381",
      bg: "#6373814f",
      lightBg: "#63738112",
    },
    success: {
      main: "#43c54a",
      bg: "#43c54a4f",
      contrastText: "#FFFFFF",
    },
    error: {
      main: "#FF5656",
      contrastText: "#FFFFFF",
    },
  },
});

export default function AddTourDialog({ CloseDialog, addDialog }) {
  const matches = useMediaQuery((theme) => theme.breakpoints.down("sm"));
  const { dispatch } = useContext(StatusContext);
  const { AddToure, toursData, toursMode, settoursMode, EditToure } =
    useContext(ToursContext);
  const defaultvalues = {
    Tname: "",
    startDate: "",
    endDate: "",
    Cname: "",
    status: false,
    Cities: [],
  };
  useEffect(() => {
    if (toursMode !== undefined) {
      let toure = toursData[toursMode] ?? defaultvalues;
      toure = { ...toure, Cname: "" };
      formik.setValues(toure, false);
      // console.log(toure);
    } else formik.handleReset();
  }, [toursMode]);

  const formik = useFormik({
    initialValues: defaultvalues,
    onSubmit: (values) => {
      toursMode !== undefined ? EditToure(values, toursMode) : AddToure(values);
      HandlClose();
      dispatch({
        type: "showSuccess",
        payload: `Toure : ${formik.values.Tname} submited`,
      });
    },
  });

  const HandlClose = () => {
    settoursMode(undefined);
    formik.handleReset();
    CloseDialog();
  };

  const ref = useRef();
  const AddCitie = () => {
    if (formik?.values?.Cname && formik.values.Cname !== "") {
      formik.setFieldValue("Cities", [
        ...formik.values.Cities,
        formik.values.Cname,
      ]);
      formik.setFieldValue("Cname", "");
      ref.current.focus();
    }
  };

  const DeleteCity = (cityIndex) => {
    const res = formik.values.Cities.filter(
      (value, index) => cityIndex !== index
    );
    formik.setFieldValue("Cities", [...res]);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    formik.handleSubmit();
  };

  const handleChange = (e) => {
    e.preventDefault();
    formik.handleChange(e);
  };

  const handleAdd = (e) => {
    if (e.key === "Enter") AddCitie();
  };

  return (
    <ThemeProvider theme={theme}>
      <Dialog
        open={addDialog}
        maxWidth="md"
        fullWidth
        fullScreen={matches}
        sx={classes.Dialog}
      >
        <DialogTitle>
          <Stack
            direction="row"
            alignItems="center"
            justifyContent="space-between"
          >
            <Typography sx={classes.DialogTitle}>
              {toursMode !== undefined ? "Edit " : "Add "}Toure
            </Typography>
            <IconButton onClick={HandlClose}>
              <CloseIcon />
            </IconButton>
          </Stack>
        </DialogTitle>
        <DialogContent sx={{ p: 0 }}>
          <List>
            <ListItem>
              <Stack
                direction="row"
                spacing={2}
                sx={{ width: "100%" }}
                alignItems="center"
              >
                <StextField
                  onChange={handleChange}
                  value={formik.values.Tname}
                  color="secondary"
                  variant="outlined"
                  name="Tname"
                  label="Toure Name: *"
                  sx={{ flexGrow: 1 }}
                />
                <FormControl component="fieldset" color="secondary">
                  <FormLabel component="legend">Toure Status</FormLabel>
                  <RadioGroup
                    row
                    aria-label="gender"
                    name="status"
                    color="secondary"
                    value={formik.values.status}
                    onChange={handleChange}
                  >
                    <FormControlLabel
                      value={true}
                      control={<Radio color="secondary" />}
                      label="Start"
                    />
                    <FormControlLabel
                      value={false}
                      control={<Radio color="secondary" />}
                      label="Stop"
                    />
                  </RadioGroup>
                </FormControl>
              </Stack>
            </ListItem>
            <ListItem>
              <Stack
                direction="row"
                spacing={2}
                sx={{ width: "100%" }}
                alignItems="center"
              >
                <StextField
                  color="secondary"
                  variant="outlined"
                  label="Start Date: *"
                  name="startDate"
                  onChange={handleChange}
                  fullWidth
                  value={formik.values.startDate}
                  sx={{ flexGrow: 1 }}
                />
                <StextField
                  color="secondary"
                  variant="outlined"
                  label="End Date: *"
                  name="endDate"
                  onChange={handleChange}
                  fullWidth
                  value={formik.values.endDate}
                  sx={{ flexGrow: 1 }}
                />
              </Stack>
            </ListItem>
            <ListItem>
              <Stack
                direction="row"
                spacing={2}
                sx={{ width: "100%" }}
                alignItems="center"
              >
                <StextField
                  inputRef={ref}
                  color="secondary"
                  variant="outlined"
                  label="City Name: *"
                  name="Cname"
                  onChange={handleChange}
                  onKeyPress={handleAdd}
                  fullWidth
                  value={formik.values.Cname}
                  sx={{ flexGrow: 1 }}
                  InputProps={{
                    endAdornment: (
                      <IconButton color="success" onClick={AddCitie}>
                        <AddCircleOutlineRoundedIcon />
                      </IconButton>
                    ),
                  }}
                />
              </Stack>
            </ListItem>
          </List>
          <CitiesTable Cities={formik.values.Cities} DeleteCity={DeleteCity} />
        </DialogContent>
        <DialogActions sx={{ p: 2 }}>
          <Button color="success" variant="contained" onClick={handleSubmit}>
            Submit
          </Button>
        </DialogActions>
      </Dialog>
    </ThemeProvider>
  );
}

const CitiesTable = ({ Cities, DeleteCity }) => {
  return (
    <Box sx={classes.BodyBox}>
      <Typography sx={classes.Title}>Cities : </Typography>
      <BodyCards Cities={Cities} DeleteCity={DeleteCity} />
    </Box>
  );
};

const BodyCards = ({ Cities, DeleteCity }) => {
  return (
    <Grid container spacing={2} sx={{ mt: 1 }}>
      {Cities && Cities.length > 0 ? (
        Cities.map((city, index) => (
          <Grid item key={index}>
            <Chip
              label={city}
              variant="outlined"
              sx={classes.chip}
              onDelete={() => DeleteCity(index)}
            />
          </Grid>
        ))
      ) : (
        <Grid item>
          <Typography sx={classes.addcity}>Please Add City</Typography>
        </Grid>
      )}
    </Grid>
  );
};

const classes = {
  chip: {
    color: "secondary.main",
    boxShadow:
      "rgba(136, 165, 191, 0.48) 3px 1px 8px 0px, rgba(255, 255, 255, 0.8) -3px -1px 8px 0px",
  },
  DialogTitle: {
    fontFamily: "Cairo",
    textTransform: "uppercase",
    fontWeight: "600",
    color: "secondary.main",
  },
  HeadBox: {
    m: 2,
    padding: "16px",
    backgroundColor: "#ededed52",
    borderRadius: "12px",
    outline: "1px solid",
    outlineColor: "#20AAE24f",
  },
  BodyBox: {
    m: 2,
    padding: "16px",
  },
  Title: {
    fontWeight: "500",
    fontFamily: "Cairo",
    color: "secondary.main",
  },
  addcity: {
    fontWeight: "300",
    color: "primary.main",
  },
  Dialog: {
    "&.MuiDialog-root": {
      backdropFilter: "blur(1px)",
    },
    "& .MuiDialog-paper": {
      borderRadius: { xs: "0", sm: "12px" },
    },
  },
};
