import AddRoundedIcon from "@mui/icons-material/AddRounded";
import CheckRoundedIcon from "@mui/icons-material/CheckRounded";
import DeleteOutlineRoundedIcon from "@mui/icons-material/DeleteOutlineRounded";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import SearchRoundedIcon from "@mui/icons-material/SearchRounded";
import {
  Box,
  Button,
  Divider,
  Grid,
  IconButton,
  InputAdornment,
  Paper,
  Stack,
  TextField,
  Typography,
  useMediaQuery,
} from "@mui/material";
import ArrowDropDownRoundedIcon from "@mui/icons-material/ArrowDropDownRounded";
import ArrowDropUpRoundedIcon from "@mui/icons-material/ArrowDropUpRounded";

import Chip from "@mui/material/Chip";
import Fab from "@mui/material/Fab";
import { border, fontFamily } from "@mui/system";
import React, { createContext, useContext, useEffect, useState } from "react";
import {
  Route,
  Routes,
  useLocation,
  useMatch,
  useNavigate,
} from "react-router";
import { StatusContext } from "../../../App";
import { DefaultTours } from "../../../assets/MasterData";
import noTour from "../../../assets/pics/noTour.svg";
import GridFactory from "../../../Tools/GridFactory";
import { SearchField } from "../../../Tools/StyledComp";
import AddFab from "./AddFab";
import AddTourDialog from "./AddTourDialog";

export const ToursContext = createContext();
export default function Tours() {
  const { dispatch } = useContext(StatusContext);
  const [addDialog, setaddDialog] = useState(false);
  const [toursData, settoursData] = useState([]);
  const [toursMode, settoursMode] = useState(undefined);
  let match = useMatch("/tours/:toure_id");
  const location = useLocation();
  let navigate = useNavigate();

  useEffect(() => {
    // dispatch({ type: "showLoading", payload:true });
    settoursData([...DefaultTours]);
    // dispatch({ type: "showLoading", payload:false });
  }, []);

  const [OpenCard, setOpenCard] = useState([]);

  const handleToggleOpen = (Tindex) => {
    let res = OpenCard;
    if (typeof res[Tindex] === "undefined") {
      res[Tindex] = "open";
    } else res[Tindex] = res[Tindex] === "open" ? "close" : "open";
    setOpenCard([...res]);
    console.log(res);
  };

  useEffect(() => {
    if (match && toursData.length > 0) {
      let id = match.params.toure_id - 1;
      if (toursData[id] !== undefined) settoursMode(id);
      else {
        dispatch({ type: "showError", payload: "No Toure with This Id" });
        navigate("/tours");
      }
    }
  }, [location.pathname, toursData]);

  const DeleteToure = (Tindex) => {
    const res = toursData.filter((value, index) => Tindex !== index);
    settoursData([...res]);
  };
  const StartToure = (Tindex) => {
    const res = toursData.map((toure, index) => {
      if (index === Tindex) toure.status = true;
      return toure;
    });
    settoursData([...res]);
  };

  const StopToure = (Tindex) => {
    const res = toursData.map((toure, index) => {
      if (index === Tindex) toure.status = false;
      return toure;
    });
    settoursData([...res]);
  };
  const AddToure = (toure) => {
    settoursData([...toursData, toure]);
  };
  const AddCityToTour = (Tindex, text) => {
    const res = toursData.map((toure, index) => {
      if (index === Tindex) toure.Cities = [...toure.Cities, text];
      return toure;
    });
    settoursData([...res]);
  };
  const IndexingToureEdit = (Tindex) => {
    settoursMode(Tindex);
    OpenDialog();
  };

  const EditToure = (data, Tindex) => {
    const res = toursData.map((toure, index) => {
      if (index === Tindex) toure = { ...data };
      return toure;
    });
    settoursData([...res]);
  };

  const DeleteCityFromTour = (Tindex, Cindex) => {
    const res = toursData.map((toure, index) => {
      if (index === Tindex)
        toure.Cities = toure.Cities.filter((city, index) => Cindex !== index);
      return toure;
    });
    settoursData([...res]);
  };

  const EditCname = (Tindex, Cindex, text) => {
    const res = toursData.map((toure, index) => {
      if (index === Tindex)
        toure.Cities = toure.Cities.map((city, index) => {
          if (Cindex === index) city = text;
          return city;
        });
      return toure;
    });
    settoursData([...res]);
  };

  const OpenDialog = () => {
    setaddDialog(true);
  };
  const CloseDialog = () => {
    setaddDialog(false);
    settoursMode(undefined);
    navigate("/tours");
  };

  return (
    <ToursContext.Provider
      value={{
        toursData,
        toursMode,
        DeleteToure,
        AddToure,
        AddCityToTour,
        DeleteCityFromTour,
        EditCname,
        IndexingToureEdit,
        settoursMode,
        EditToure,
        StartToure,
        StopToure,
        OpenCard,
        handleToggleOpen,
      }}
    >
      <Paper sx={classes.root}>
        <AddNsearchButton OpenDialog={OpenDialog} />
        {toursData.length > 0 ? <Cards /> : <NoTours />}
        <AddTourDialog CloseDialog={CloseDialog} addDialog={addDialog} />
        <Routes>
          <Route
            path=":toure_id"
            element={
              <AddTourDialog CloseDialog={CloseDialog} addDialog={true} />
            }
          />
        </Routes>
        <AddOnScroll OpenDialog={OpenDialog} addDialog={addDialog} />
      </Paper>
    </ToursContext.Provider>
  );
}

const AddOnScroll = (props) => {
  return props.addDialog ? (
    false
  ) : (
    <AddFab {...props}>
      <Fab variant={"circular"} color="primary" size="small" sx={classes.fab}>
        <AddRoundedIcon sx={{ color: "primary.main" }} />
      </Fab>
    </AddFab>
  );
};

const NoTours = () => {
  return <Box sx={classes.NoTours} />;
};

const Cards = () => {
  const { toursData } = useContext(ToursContext);
  return (
    <GridFactory
      items={toursData}
      gridSize={gridSize}
      child={<TourCard />}
      spacing={2}
    />
  );
};
const AddNsearchButton = ({ OpenDialog }) => {
  return (
    <Stack
      direction={{ xs: "column", md: "row" }}
      justifyContent="space-between"
      alignItems="center"
      spacing={2}
      sx={{ marginBottom: 4, position: "relative" }}
    >
      <SearchGrid />
      <Button
        variant="contained"
        startIcon={<AddRoundedIcon />}
        sx={classes.addButton}
        onClick={OpenDialog}
      >
        Add Toure
      </Button>
    </Stack>
  );
};

const SearchGrid = () => {
  return (
    <Grid container spacing={2}>
      <Grid
        item
        xs={gridSize.xs}
        md={gridSize.md}
        lg={gridSize.lg}
        xl={gridSize.xl}
      >
        <SearchField
          fullWidth
          placeholder="Search"
          InputProps={{
            endAdornment: (
              <IconButton position="end">
                <SearchRoundedIcon sx={{ color: "text.primary" }} />
              </IconButton>
            ),
          }}
        />
      </Grid>
    </Grid>
  );
};

const TourCard = ({ item, Tindex }) => {
  const { dispatch } = useContext(StatusContext);
  const {
    DeleteToure,
    AddCityToTour,
    EditCname,
    toursData,
    IndexingToureEdit,
    OpenCard,
    handleToggleOpen,
  } = useContext(ToursContext);

  const createModeArray = () => {
    let res = [];
    res = item.Cities.map((city, index) => {
      return { mode: "edit", value: city };
    });
    return res;
  };

  const [mode, setmode] = useState([]);

  useEffect(() => {
    setmode([...createModeArray()]);
  }, [toursData[Tindex].Cities]);

  const handleChange = (e) => {
    let res = mode?.map((item, index) => {
      if (index == e.target.name) item.value = e.target.value;
      return item;
    });
    setmode([...res]);
  };
  // Edit Citie Mode
  const ChangeMode = (index) => {
    let res = mode ?? [];
    res[index].Editmode =
      res[index] && res[index].Editmode && res[index].Editmode === "submit"
        ? "edit"
        : "submit";
    setmode([...res]);
    dispatch({
      type:
        res[index].Editmode && res[index].Editmode === "submit"
          ? "showInfo"
          : "showSuccess",
      payload:
        res[index].Editmode && res[index].Editmode === "submit"
          ? "Edit Mode"
          : "Submited",
    });
  };

  const AddCity = () => {
    dispatch({
      type: "showDialog",
      payload: {
        title: "Add City",
        type: "info",
        field: { label: "Enter City Name", required: true },
        buttonLabel: "ADD",
        callback: (enteredText) => {
          AddCityToTour(Tindex, enteredText);
          mode.push({ Editmode: "edit", value: enteredText });
          setmode([...mode]);
        },
      },
    });
  };

  const HandleEdit = (index) => {
    if (!mode[index])
      mode[index] = { ...mode[index], value: toursData[Tindex].Cities[index] };
    if (mode && mode[index].Editmode && mode[index].Editmode === "submit") {
      if (mode && mode[index].value !== "") {
        dispatch({
          type: "showDialog",
          payload: {
            title: "Edit this City",
            message: `From ${item.Cities[index]} To ${mode[index].value}`,
            type: "info",
            buttonLabel: "EDIT",
            callback: () => {
              EditCname(Tindex, index, mode[index].value);
              ChangeMode(index);
            },
          },
        });
      }
    } else ChangeMode(index);
  };
  const HandleDelete = () => {
    dispatch({
      type: "showDialog",
      payload: {
        title: "Delete this Toure",
        message: `Toure name: ${item.Tname}`,
        type: "negative",
        buttonLabel: "Delete",
        callback: () => DeleteToure(Tindex),
      },
    });
  };
  const matches = useMediaQuery((theme) => theme.breakpoints.down("md"));

  return (
    <Box
      sx={
        matches
          ? {
              ...classes.card,
              ...(OpenCard[Tindex] === "open" && {
                height: "calc(100% - 16px)",
              }),
            }
          : classes.card
      }
    >
      <Box>
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
          sx={classes.Bg}
        >
          <IconButton
            id="showDown"
            onClick={() => handleToggleOpen(Tindex)}
            sx={classes.down}
            disableRipple
          >
            {OpenCard[Tindex] === "open" ? (
              <ArrowDropUpRoundedIcon />
            ) : (
              <ArrowDropDownRoundedIcon />
            )}
          </IconButton>
          <Typography sx={classes.toureName}>{item.Tname}</Typography>
          <Stack direction="row" spacing={1} alignItems="center">
            <IconButton
              onClick={() => IndexingToureEdit(Tindex)}
              // color="secondary"
            >
              <EditOutlinedIcon />
            </IconButton>
            <IconButton color="primary" onClick={HandleDelete}>
              <DeleteOutlineRoundedIcon />
            </IconButton>
          </Stack>
        </Stack>
        <DateInfos item={item} />

        <Stack direction="column" spacing={2}>
          {item?.Cities?.map((city, index) => (
            <Stack direction="column" spacing={2} key={index}>
              <EditCityName
                mode={mode}
                HandleEdit={HandleEdit}
                index={index}
                city={city}
                Tindex={Tindex}
                handleChange={handleChange}
                setmode={setmode}
              />
              {index + 1 < item?.Cities?.length ? <Divider /> : false}
            </Stack>
          ))}
        </Stack>
      </Box>
      <AddSubmit
        status={item.status}
        AddCity={AddCity}
        name={item.Tname}
        Tindex={Tindex}
      />
    </Box>
  );
};

const DateInfos = ({ item }) => {
  return (
    <>
      <Stack
        direction="row"
        spacing={2}
        alignItems="center"
        sx={{
          flexGrow: 1,
          paddingBottom:{xs:1, md:2},
          fontWeight: "400",
          fontFamily: "Cairo",
        }}
      >
        <Typography sx={{ fontWeight: "400", fontFamily: "Cairo" }}>
          {item.startDate}
        </Typography>
        <Divider sx={{ flexGrow: 1, backgroundColor: "transparent" }}>
          to
        </Divider>
        <Typography sx={{ fontWeight: "400", fontFamily: "Cairo" }}>
          {item.endDate}
        </Typography>
      </Stack>
      <Divider sx={{ marginBottom: "16px" }} />
    </>
  );
};

const EditCityName = ({
  mode,
  HandleEdit,
  index,
  city,
  Tindex,
  handleChange,
  setmode,
}) => {
  return (
    <Stack
      key={index}
      direction="row"
      justifyContent="space-between"
      alignItems="center"
    >
      {mode[index] && mode[index].Editmode === "submit" ? (
        <TextField
          variant="standard"
          color="secondary"
          name={index.toString()}
          onChange={handleChange}
          value={mode[index].value ?? ""}
        />
      ) : (
        <Chip clickable label={city} variant="outlined" sx={classes.chip} />
      )}
      <EditDelete
        mode={mode}
        HandleEdit={HandleEdit}
        index={index}
        Tindex={Tindex}
        setmode={setmode}
      />
    </Stack>
  );
};

const AddSubmit = ({ status, AddCity, name, Tindex }) => {
  const { dispatch } = useContext(StatusContext);
  const { StopToure, StartToure } = useContext(ToursContext);
  const HandleStart = () => {
    dispatch({
      type: "showDialog",
      payload: {
        title: "Start This Toure",
        message: `Toure name: ${name}`,
        type: "positive",
        buttonLabel: "START",
        callback: () => StartToure(Tindex),
      },
    });
  };
  const HandleDectivate = () => {
    dispatch({
      type: "showDialog",
      payload: {
        title: "Stop This Toure",
        message: `Toure name: ${name}`,
        type: "negative",
        buttonLabel: "STOP",
        callback: () => StopToure(Tindex),
      },
    });
  };
  return (
    <Stack
      direction="row"
      spacing={2}
      sx={{ pt: "1rem" }}
      justifyContent="flex-end"
    >
      <Button
        variant="outlined"
        startIcon={<AddRoundedIcon />}
        color="third"
        sx={classes.addcity}
        onClick={AddCity}
        fullWidth
      >
        Add City
      </Button>
      {status === "true" || status === true ? (
        <Button
          variant="contained"
          fullWidth
          color="error"
          sx={{ borderRadius: "12px" }}
          onClick={HandleDectivate}
        >
          Stop Toure
        </Button>
      ) : (
        <Button
          variant="contained"
          fullWidth
          color="success"
          sx={{ borderRadius: "12px" }}
          onClick={HandleStart}
        >
          Start Toure
        </Button>
      )}
    </Stack>
  );
};

const EditDelete = ({ mode, HandleEdit, index, Tindex, setmode }) => {
  const { dispatch } = useContext(StatusContext);
  const { DeleteCityFromTour } = useContext(ToursContext);

  const HandleDelete = () => {
    if (mode[index] && mode[index].Editmode !== "submit")
      dispatch({
        type: "showDialog",
        payload: {
          title: "Delete this City",
          message: mode[index].value,
          type: "negative",
          buttonLabel: "DELETE",
          callback: () => {
            DeleteCityFromTour(Tindex, index);
            const res = mode.filter((e, i) => i !== index);
            setmode([...res]);
            console.log(res);
          },
        },
      });
    else
      dispatch({
        type: "showError",
        payload: "Please Submit Data before Deleting",
      });
  };
  return (
    <Stack direction="row" spacing={1}>
      <IconButton
        name={index}
        color={
          mode[index] && mode[index].Editmode === "submit"
            ? "success"
            : "default"
        }
        onClick={() => HandleEdit(index)}
      >
        {mode[index] && mode[index].Editmode === "submit" ? (
          <CheckRoundedIcon />
        ) : (
          <EditOutlinedIcon />
        )}
      </IconButton>
      <IconButton color="primary" onClick={HandleDelete}>
        <DeleteOutlineRoundedIcon />
      </IconButton>
    </Stack>
  );
};

