import * as React from "react";
import Box from "@mui/material/Box";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepLabel from "@mui/material/StepLabel";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { Paper, Stack } from "@mui/material";
import StepConnector, {
  stepConnectorClasses,
} from "@mui/material/StepConnector";
import { styled } from "@mui/material/styles";
import step1 from "../../../assets/pics/s1.png";
import step2 from "../../../assets/pics/s2.png";
import step3 from "../../../assets/pics/s3.png";

const steps = [
  { label: "Depot de colis dans la filiale", icon: step1 },
  { label: "Embarasument fe colis", icon: step2 },
  { label: "Traversee en France", icon: step3 },
  { label: "Depot de colis dans la filiale", icon: step1 },
  { label: "Embarasument fe colis", icon: step2 },
  { label: "Traversee en France", icon: step3 },
  { label: "Traversee en France", icon: step1 },
];

export default function TableTimeLine() {
  const [activeStep, setActiveStep] = React.useState(0);
  const [skipped, setSkipped] = React.useState(new Set());


  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    let newSkipped = skipped;
    if (isStepSkipped(activeStep)) {
      newSkipped = new Set(newSkipped.values());
      newSkipped.delete(activeStep);
    }
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  const GoToStep = (step) => {
    setActiveStep(step);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <Paper sx={classes.paper}>
      <Typography paragraph sx={classes.Title}>
        Tracking
      </Typography>
      <Stepper
        activeStep={activeStep}
        connector={<Connector />}
        alternativeLabel
      >
        {steps.map((item, index) => {
          const stepProps = {};
          const labelProps = {};
          return (
            <Step {...stepProps} key={index}>
              <StepLabel
                {...labelProps}
                StepIconComponent={StepHandler}
                StepIconProps={{
                  icon: item.icon,
                  index: index,
                  GoToStep: GoToStep,
                }}
              >
                {item.label}
              </StepLabel>
            </Step>
          );
        })}
      </Stepper>
      <Stack
        direction="row"
        justifyContent={{ xs: "flex-start", md: "flex-end" }}
        spacing={2}
        sx={{ pt: 3 }}
      >
        <Button onClick={handleReset} sx={classes.restartButton}>
          Restart
        </Button>
        <Button onClick={() => GoToStep(8)} sx={classes.finishButton}>
          Finish
        </Button>
      </Stack>
    </Paper>
  );
}

const StepHandler = ({ icon, index, active, completed, GoToStep }) => {
  return (
    <IconImg
      src={icon}
      active={active ? "true" : undefined}
      completed={completed ? "true" : undefined}
      onClick={() => GoToStep(index)}
    />
  );
};

const IconImg = styled("img")(({ theme, active, completed }) => ({
  width: "4em",
  height: "4em",
  zIndex: 2,
  cursor: "pointer",
  border: "3px solid",
  borderColor: theme.palette.primary.lightBg,
  backgroundColor: "#FFFFFF",
  borderRadius: "50%",
  transition: "all .2s ease",
  ...(active && {
    borderColor: theme.palette.primary.bg,
    transform: "scale(1.2)",
    boxShadow:
      " rgba(0, 0, 0, 0.1) 0px 0px 6px, rgba(0, 0, 0, 0.22) 0px 0px 12px",
  }),
  ...(completed && {
    borderColor: theme.palette.primary.main,
  }),
}));

const Connector = styled(StepConnector)(({ theme }) => ({
  [`&.${stepConnectorClasses.alternativeLabel}`]: {
    top: "2em",
    zIndex: 1,
    left: "calc(-50% + 16px)",
    right: "calc(50% + 16px)",
  },
  [`&.${stepConnectorClasses.active}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      borderColor: theme.palette.primary.bg,
    },
  },
  [`&.${stepConnectorClasses.completed}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      borderColor: theme.palette.primary.main,
    },
  },
  [`& .${stepConnectorClasses.line}`]: {
    borderColor: theme.palette.primary.lightBg,
    borderTopWidth: 3,
    borderRadius: 1,
  },
}));

const classes = {
  paper: {
    py: "2rem",
    px: 3,
    // width: "100%",
    borderRadius: "12px",
    boxShadow:
      "rgb(136 165 191 / 48%) 6px 2px 16px 0px, rgb(255 255 255 / 80%) -6px -2px 16px 0px",
    overflow: "auto",
  },
  steps: {
    width: "4em",
    height: "4em",
    zIndex: 2,
    border: "3px solid",
    borderColor: "#FF56564f",
    backgroundColor: "#FFFFFF",
    borderRadius: "50%",
  },
  Title: {
    fontFamily: "Cairo",
    fontWeight: "600",
    fontSize: "1.5rem",
    pb: 2,
  },
  finishButton: {
    width: { xs: "100px", md: "150px" },
    borderRadius: "12px",
    color: "#FFFFFF",
    borderColor: "text.primary",
    "&:hover": {
      backgroundColor: "text.buttonBg",
    },
    backgroundColor: "text.primary",
    height: "56px",
  },
  restartButton: {
    width: { xs: "100px", md: "150px" },
    borderRadius: "12px",
    color: "text.primary",
    border: "4px solid",
    borderColor: "text.primary",
    "&:hover": {
      backgroundColor: "text.lightBg",
    },
    height: "56px",
  },
};
